import axios from "axios";
import React, { useEffect } from "react";
import { useNavigate } from "react-router-dom";



const DeleteAccount = () => {
  const navigate = useNavigate()
  const token = localStorage.getItem("token")
  const instance = axios.create({
    baseURL: '/deleteuser'
  })
  if (localStorage.getItem("token") !== null) {
    instance.defaults.headers.common['Authorization'] = "Bearer " + localStorage.getItem("token")
    if (window.confirm("Haluatko varmasti poistaa kyttjn?")) {
      instance.post("/deleteuser").then(console.log).catch(console.log)
      axios.post("/customview/deletebyowner", {},
      {
        params: {
          token
        }
      }
    ).then(response => {
      console.log(response)
    }).catch(error => {
      alert("Nkymien poisto eponnistui")
      console.log(error)
    })
      localStorage.removeItem("token")
    }
  }
  else {
    alert("Et ole kirjautunut sisn.")
  }

  useEffect(() => {
    navigate('/')
  }, [])
  

}

export default DeleteAccount